.resume-card {
  margin: 10px;
  margin-top: 3vh;
  background-color: #ffffff;
  font-size: 16px;
  border-radius: 15px;
}
.row {
  margin-top: 10vh;
  margin: auto;
  justify-content: center;
}

.resume-card .project {
  width: 245px;
  font-size: 16px;
  border-radius: 15px;
  border: 1.5px #00000080 solid;
  padding: 3px 4px;
}

.resume-card .res-card1 .react-select {
  border: 1px #000 solid;
  font-size: large;
}
.react-select div {
  border: none;
}
.user-info {
  display: flex;
  margin-bottom: 15px;
  color: #616670;
  border-bottom: 1px solid #f3f3f3;
}

.img-profile {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-bottom: 15px;
}
.user-info .meta {
  padding: 8px 15px;
}

.user-info .meta li span {
  display: inline-block;
  width: 100px;
  margin-right: 5px;
  text-align: right;
}
.MuiCardContent-root {
  padding: 4px 6px !important;
}
.meta li a {
  color: #616670;
}
.name {
  text-align: left;
  letter-spacing: 0px;
  font-size: 18px;
  font-weight: 500;
  font-family: Poppins;
  color: #131212;
  opacity: 1;
}
.email {
  font-size: 15px;
  font-family: Poppins;
}
.eyeicon {
  color: #04724d !important;
  width: 15px;
  margin-top: -10px;
  margin-left: 10px;
}
.container-fluid {
  margin-top: 101px;
  color: #000 !important;
}

.card {
  color: #757575 !important;
  background-color: #fff !important;
  align-items: center !important;
  background: url("https://i.imgur.com/9djv5sH.jpg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right
    calc(110px + (150 - 110) * ((100vw - 360px) / (1600 - 360))) bottom !important;
}

h2 {
  font-size: calc(
    25px + (45 - 25) * ((100vw - 360px) / (1600 - 360))
  ) !important;
}

h5 {
  font-size: calc(
    20px + (28 - 20) * ((100vw - 360px) / (1600 - 360))
  ) !important;
}

.fa {
  cursor: pointer;
  font-size: 21px;
  margin: 5px 10px 5px 10px !important;
}

button {
  font-size: calc(
    12px + (13 - 12) * ((100vw - 360px) / (1600 - 320))
  ) !important;
  padding-left: calc(
    20px + (28 - 20) * ((100vw - 360px) / (1600 - 360))
  ) !important;
  padding-right: calc(
    20px + (28 - 20) * ((100vw - 360px) / (1600 - 360))
  ) !important;
  padding-top: calc(
    10px + (12 - 10) * ((100vw - 360px) / (1600 - 360))
  ) !important;
  padding-bottom: calc(
    10px + (12 - 10) * ((100vw - 360px) / (1600 - 360))
  ) !important;
}

button:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  outline-width: 0;
}

.row li {
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Poppins", sans-serif;
}

input {
  border: 2px solid green !important;
}

.input-group-lg > .custom-select,
.input-group-lg > .form-control:not(textarea) {
  height: calc(40px + (48 - 40) * ((100vw - 360px) / (1600 - 360))) !important;
}

input::-webkit-input-placeholder {
  padding-left: 5px !important;
  transform: translate3d(0, -4px, 0);
}

input::placeholder {
  vertical-align: middle !important;
  font-size: calc(
    12px + (14 - 12) * ((100vw - 360px) / (1600 - 360))
  ) !important;
}

input:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  outline-width: 0;
}

img {
  vertical-align: middle !important;
  width: 100%;
  height: auto;
  width: calc(17px + 6 * ((100vw - 320px) / 680));
}
.img-fluid {
  width: 50%;
  height: 100%;
}
.foo-header {
  font-size: 21px !important;
  font-weight: bold;
  margin-bottom: calc(
    40px + (48 - 40) * ((100vw - 360px) / (1600 - 360))
  ) !important;
  letter-spacing: 0px;
  color: #131212;
  opacity: 1;
  font: normal normal 600 19px/29px Poppins;
}
.flex-1 {
  display: flex;
  justify-content: space-between;
}
.para-heading {
  font-size: 16px;
}
.para-number {
  font-size: 20px;
  color: #04724d;
  font-weight: 500;
}
.para-style {
  font-size: 12px;
  color: #04724d;
  font-weight: 500;
}
.MuiRadio-colorSecondary.Mui-checked {
  color: #04724d;
}
.MuiSvgIcon-root {
  color: #04724d;
}
.iconsvg {
  background-color: #ddd;
  margin-right: 10px;
}
img {
  margin-right: 10px;
}
.subscription {
  border-radius: 30px !important;
}
.rowcard {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
}
.cardpara {
  color: #3d3d3d;
  margin-bottom: 20px;
  font-size: 14px;
}
.btn-group .btn button {
  padding: 5px 15px;
  background-color: #28a745;
  color: #fff;
  border-radius: 3px;
  margin-left: -2px;
}
.btn-group button {
  margin: 0 10px;
  text-decoration: none;
  color: #000;
}
.makeStyles-paper-9 {
  top: 0 !important;
  margin: 139px !important;
  padding: 100px !important;
}
.col-md-3 {
  margin-bottom: 50px;
  max-width: 100% !important;
}
.MuiPaper-elevation1 {
  box-shadow: unset !important;
  border: 1px solid #d4e6e0;
  border-radius: 8px;
  padding: 0px;
  padding-top: 6px;
}

.editIconSetting {
  color: #04724d !important;
  width: 15px;
  margin-top: -10px;
  margin-left: 10px;
}

.EditPasswordSetting {
  .EditModal {
    position: absolute;
    width: 500px;
    background-color: #fff;
    left: calc(100% - 50% - 250px);
    top: calc(100% - 50% - 150px);
    border: none;
    padding: 30px;
  }
  .EditModal:focus-visible {
    border: none;
  }
  .editTextField-div {
    width: 100%;

    .MuiFormControl-root {
      width: 100%;
      .MuiInputBase-input {
        padding: 6px 10px 7px;
      }
    }
  }
  .close {
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
  }
  .button-div {
    display: flex;
    justify-content: center;
    margin-top: 5px;
  }
}
